/* HOME-NAVBAR */
.home-navbar {
    width: 100%;
    height: 773px;
    display: flex;
    background: url("/public/img/banner.webp");
    background-size: cover;
  }
  
  .home-navbar .navbar-container {
    margin-top: 55px;
    height: 642px;
  }
  
  .home-navbar .navbar-container .menu {
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .home-navbar .navbar-container .menu .brand-logo {
    width: 125px;
    height: 30px;
  }
  
  .home-navbar .navbar-container .menu .links {
    min-width: 700px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .home-navbar .navbar-container .menu .links .login-button {
    background: #3776cc;
    color: #f8f8ff;
  }
  
  .home-navbar .navbar-container .menu .user-avatar {
    width: 50px;
    height: 50px;
    border-radius: 24px;
  }
  
  .home-navbar a {
    text-decoration: none;
  }
  
  .home-navbar .hover-line a {
    color: #f8f8ff;
    z-index: 1000;
  }
  
  .home-navbar .underline {
    display: block;
    content: "";
    border-bottom: solid 2px #d7b686;
  }
  
  .home-navbar .hover-line:after {
    display: block;
    content: "";
    border-bottom: solid 2px #d7b686;
    transform: scaleX(0);
    transition: transform 150ms ease-in-out;
  }
  
  .home-navbar .hover-line:hover:after {
    transform: scaleX(1);
  }
  
  .home-navbar div.hover-line:after {
    transform-origin: 0% 50%;
  }
  
  .home-navbar .navbar-container .header-frame {
    height: 462px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 142px;
  }
  
  .home-navbar .navbar-container .header-frame .detail {
    width: 510px;
    display: flex;
    flex-direction: column;
  }
  
  .home-navbar .navbar-container .header-frame .detail .head-main-txt {
    width: 450px;
    height: 150px;
    font-family: "Roboto Serif";
    font-size: 50px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.75%;
    color: #f8f8ff;
  }
  
  .home-navbar .navbar-container .header-frame .detail .welcome-txt {
    width: 510px;
    height: 42px;
    font-family: "Roboto Serif";
    font-style: normal;
    font-size: 36px;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.54%;
    color: #d7b686;
  }
  
  .home-navbar .navbar-container .header-frame .detail .service-txt {
    margin-top: 23px;
    width: 153px;
    height: 23px;
    font-family: "Roboto Serif";
    font-style: normal;
    font-size: 20px;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.3%;
    color: #d5d5d5;
  }
  
  .home-navbar .navbar-container .header-frame .detail .signup {
    margin-top: 73px;
  }
  
  .home-navbar .navbar-container .header-frame .detail .signup .signup-button {
    width: 210px;
    height: 60px;
    border-radius: 4px;
    background: #1976d2;
    font-family: "Poppins";
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: #f8f8ff;
  }
  
  .home-navbar .navbar-container .logo-frame {
    margin-top: 43px;
    width: 690px;
    height: 419px;
  }
  
  .home-navbar .navbar-container .logo-frame .logo-img {
    width: 100%;
    height: 100%;
    background: url("/public/img/logo.webp");
    background-size: contain;
    background-repeat: no-repeat;
    filter: invert(25%);
  }
  
